body {
  margin: 0;
  padding: 0;
  /* font-family: 'Noto Sans KR', sans-serif; */
  font-family: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p,h1,h2,h3,h4,h5,h6 {
  margin:0;
  padding:0;
}

h1{
  font-size: 36px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*/////////////////
////// CARDS //////
/////////////////*/

.Card {
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 2px 8px;
  display: inline-block;
}

.Carousel{
  display:flex;
  .arrow {
    width:33.333%;
    float:left;
    height:100%;

    .left {
      float:right;
    }
    .right {
      float:left;
    }
  }

  .view-window {
    width:33.333%;
    float:left;
  }

  .Card {
    height: 400px;
    width: 300px;

    .image {
      height: 80%;
      width: 100%;
      overflow: hidden;
      margin-left:auto;
      margin-right:auto;

      img {
        margin-left:-100px;
        margin-right:-100px;
        height: 100%;
        width: auto;
      }
    }

    .animate{
      background:#53a7ea;
    }
  }
}

/*/////////////////
////// BOXES //////
/////////////////*/

.Box--xxs {
  padding: 2px;
}
.Box--xs {
  padding: 4px;
}
.Box--sm {
  padding: 8px;
}
.Box--md {
  padding: 16px;
}
.Box--md-lg {
  padding: 24px;
}
.Box--lg {
  padding: 32px;
}
.Box--xl {
  padding: 64px;
}


/*/////////////////
//// UTILITIES ////
/////////////////*/

.font-bold {
  font-weight: bold;
}